:root {
  --paleblue: hsl(225, 100%, 94%);
  --brightblue: hsl(245, 75%, 52%);
  --brightbluehover: hsl(245, 60%, 60%);
  --verypaleblue: hsl(225, 100%, 98%);
  --desaturatedblue: hsl(224, 23%, 55%);
  --darkblue: hsl(223, 47%, 23%);
}

:root {
  --red-hat-display: "Red Hat Display", sans-serif;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--red-hat-display);
}

h1,
h2,
h3 {
  margin-top: 0;
  margin-bottom: 0;
}

p {
  margin-top: 0;
  margin-bottom: 0;
}

a, a:visited, a:active {
  text-decoration: none;
}

button {
  border: none;
  background: none;
}

body {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background: no-repeat top/100% url("../../../images/pattern-background-mobile.svg"), var(--paleblue);
}
@media (min-width: 40em) {
  body {
    background: no-repeat top/100% url("../../../images/pattern-background-desktop.svg"), var(--paleblue);
  }
}
@media (min-width: 64em) {
  body {
    background: no-repeat top/100% url("../../../images/pattern-background-desktop.svg"), var(--paleblue);
  }
}

.container {
  width: 100%;
  min-width: 20.4375rem;
  min-height: 35.4375rem;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 1.25rem 0;
}

.card {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 20.4375rem;
  background: white;
  border-radius: 1.25rem;
  overflow: hidden;
}
@media (min-width: 40em) {
  .card {
    width: 28.125rem;
  }
}
@media (min-width: 64em) {
  .card {
    width: 31.25rem;
  }
}
.card__img-header {
  width: 20.4375rem;
}
@media (min-width: 40em) {
  .card__img-header {
    width: 28.125rem;
  }
}
@media (min-width: 64em) {
  .card__img-header {
    width: 32.5rem;
  }
}
.card__title {
  color: var(--darkblue);
  font-weight: 900;
  margin-top: 1.625rem;
  font-size: 1.5rem;
}
@media (min-width: 40em) {
  .card__title {
    margin-top: 2.25rem;
    font-size: 2rem;
  }
}
@media (min-width: 64em) {
  .card__title {
    margin-top: 2.25rem;
    font-size: 2.25rem;
  }
}
.card__description {
  color: var(--desaturatedblue);
  text-align: center;
  font-size: 1rem;
  margin-top: 0.8125rem;
  padding: 0 2rem;
  line-height: 1.6875rem;
}
@media (min-width: 40em) {
  .card__description {
    font-size: 1rem;
    margin-top: 1rem;
    padding: 0 4.9375rem;
  }
}
@media (min-width: 64em) {
  .card__description {
    font-size: 1.125rem;
    margin-top: 1rem;
    padding: 0 4.9375rem;
    line-height: 2.125rem;
  }
}
.card__plan {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(2, 1fr);
  align-items: center;
  justify-items: center;
  width: 18rem;
  height: 5.0625rem;
  margin-top: 0.8125rem;
  border-radius: 0.75rem;
  background-color: var(--verypaleblue);
  font-size: 1rem;
}
@media (min-width: 40em) {
  .card__plan {
    width: 22.375rem;
  }
}
@media (min-width: 64em) {
  .card__plan {
    width: 24.25rem;
  }
}
.card__plan img {
  grid-row: 1/3;
}
.card__plan__type {
  grid-row: 1/2;
  grid-column: 2/3;
  position: relative;
  top: 0.375rem;
  right: 1.125rem;
  color: var(--darkblue);
  font-weight: 900;
}
@media (min-width: 40em) {
  .card__plan__type {
    right: 2.375rem;
  }
}
@media (min-width: 64em) {
  .card__plan__type {
    right: 2.8125rem;
  }
}
.card__plan__price {
  grid-row: 2/3;
  grid-column: 2/3;
  position: relative;
  bottom: 0.375rem;
  right: 1.125rem;
  color: var(--desaturatedblue);
  font-weight: 500;
}
@media (min-width: 40em) {
  .card__plan__price {
    right: 2.375rem;
  }
}
@media (min-width: 64em) {
  .card__plan__price {
    right: 2.8125rem;
  }
}
.card__plan__change-link {
  grid-row: 1/3;
  text-decoration: underline;
  color: var(--brightblue);
}
.card__plan__change-link:hover {
  color: var(--brightbluehover);
}
.card__btn-group {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 18rem;
  margin-top: 2rem;
  margin-bottom: 1.625rem;
}
@media (min-width: 40em) {
  .card__btn-group {
    width: 22.375rem;
  }
}
@media (min-width: 64em) {
  .card__btn-group {
    width: 24.25rem;
  }
}
.card__btn-group__btn-payment, .card__btn-group__btn-cancel {
  width: inherit;
  border-radius: 12px;
  font-size: 1rem;
  font-weight: 700;
  height: 3.125rem;
}
.card__btn-group__btn-payment:hover, .card__btn-group__btn-cancel:hover {
  cursor: pointer;
}
.card__btn-group__btn-payment {
  background: var(--brightblue);
  color: white;
}
.card__btn-group__btn-payment:hover {
  background: var(--brightbluehover);
}
.card__btn-group__btn-cancel {
  color: var(--desaturatedblue);
}
.card__btn-group__btn-cancel:hover {
  color: var(--darkblue);
}